import React, { useEffect, useState } from 'react';
import './styles/style.scss';
import Landing from './components/Landing';
import Project from './components/Project';
import projects from './projects';
import About from './components/About';
import Footer from './components/Footer';

function App() {
  const [windowSize, setWindowSize] = useState({
    width: window.innerWidth,
    height: window.innerHeight
  });
  const [targetEl, setTargetEl] = useState(null);
  const projectList = [];

  useEffect(() => {
    function handleResize() {
      setWindowSize({
        width: window.innerWidth,
        height: window.innerHeight
      });
    }

    let vh = windowSize.height * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  });

  useEffect(() => {
    const targetEl = projectList.find((item) => item.current.dataset.name === 'To Do');
    setTargetEl(targetEl);
  }, []);

  const scrollToProject = () => {
    if (targetEl) {
      targetEl.current.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <div className="App">
      <Landing scrollToProject={scrollToProject} />
      {projects.map((project) => (
        <Project key={project.title} project={project} projectList={projectList} />
      ))}
      <About />
      <Footer />
    </div>
  );
}

export default App;
